import React from "react";
import {
  Modal,
  Container,
  Card,
  CardMedia,
  CardContent,
  CardHeader,
  IconButton,
  Grid,
  Button,
  Typography
} from "@material-ui/core";
import { Public, GitHub } from "@material-ui/icons";
import useStyles from "./styles";

import data from "../../projects";

const ProjectDetails: React.FC<{
  selected: number;
  isOpen: boolean;
  onRequestClose: () => void;
}> = ({ selected, isOpen, onRequestClose }) => {
  const {
    modal,
    modalContent,
    imageWrapper,
    container,
    imageClass
  } = useStyles();

  if (selected === -1) return <></>;
  const foundData = data.find((i) => i.id === selected)!;

  return (
    <Modal
      className={modal}
      open={isOpen}
      onClose={onRequestClose}
      aria-labelledby="simple-modal-title"
      aria-describedby="simple-modal-description"
    >
      <Container className={container}>
        <Card className={modalContent}>
          <CardMedia className={imageWrapper}>
            <img className={imageClass} src={foundData.image} alt="aaaa" />
          </CardMedia>
          <CardHeader
            title={foundData.title}
            style={{ alignItems: "flex-start", marginTop: "10px" }}
            action={
              <Grid container spacing={2} alignItems="center" direction="row">
                <Grid item xs>
                  <IconButton
                    component="a"
                    target="_blank"
                    href={foundData.site}
                  >
                    <Public fontSize="large" color="inherit" />
                  </IconButton>
                </Grid>
                <Grid item xs>
                  <IconButton
                    component="a"
                    target="_blank"
                    href={foundData.github}
                  >
                    <GitHub fontSize="large" color="inherit" />
                  </IconButton>
                </Grid>
                <Grid item xs>
                  <Button
                    onClick={onRequestClose}
                    variant="contained"
                    color="secondary"
                  >
                    Close
                  </Button>
                </Grid>
              </Grid>
            }
          />
          <CardContent>
            <Typography
              variant="body1"
              dangerouslySetInnerHTML={{ __html: foundData.info }}
            />
          </CardContent>
        </Card>
      </Container>
    </Modal>
  );
};

export default ProjectDetails;
