import React from "react";
import { Typography, Hidden } from "@material-ui/core";
import lucasPic from "../../images/lucas.jpg";
import useStyles from "./styles";

export default function IntroSection() {
  const { introSection, image, jobTitle, introText } = useStyles();
  return (
    <section className={introSection}>
      <Hidden mdUp>
        <Typography className={introText} variant="h3" component="h1">
          Hi, I am <br />
          <strong>Lucas Mercado</strong>
        </Typography>
      </Hidden>
      <Hidden smDown>
        <Typography className={introText} variant="h2" component="h1">
          Hi, I am <br />
          <strong>Lucas Mercado</strong>
        </Typography>
      </Hidden>
      <Typography className={jobTitle} variant="h5" component="h2">
        Fullstack Web Developer
      </Typography>
      <img className={image} src={lucasPic} alt="Lucas" />
    </section>
  );
}
