import { makeStyles } from "@material-ui/core";

export default makeStyles(({ palette }) => ({
  footer: {
    height: 250,
    padding: "3em 0",
    background: "#111"
  },
  link: {
    fontFamily: "'Playfair Display', serif",
    textDecoration: "none",
    "&:hover": {
      color: palette.primary.dark,
      textDecoration: "underline"
    }
  }
}));
