import { makeStyles } from "@material-ui/core";

export default makeStyles(({ breakpoints }) => ({
  menuButton: {
    marginRight: "auto"
  },
  navBar: {
    [breakpoints.up("xs")]: {
      minHeight: 56,
      maxHeight: 56
    },
    [breakpoints.up("sm")]: {
      minHeight: 64,
      maxHeight: 64
    }
  }
}));
