import React from "react";
import { AppBar, Toolbar, IconButton } from "@material-ui/core";
import {
  Menu as HamburgerIcon,
  WbSunny as SunIcon,
  Brightness2 as MoonIcon
} from "@material-ui/icons";
import Theme from "../../theme";
import useStyles from "./styles";

const NavBar = () => {
  const { darkState, toggleTheme } = Theme.useContainer();
  const { menuButton, navBar } = useStyles();

  return (
    <AppBar
      className={navBar}
      component="nav"
      elevation={0}
      position="static"
      color="transparent"
    >
      <Toolbar>
        {/* <IconButton className={menuButton} edge="start" color="inherit">
          <HamburgerIcon />
        </IconButton> */}
        <IconButton
          style={{ marginLeft: "auto" }}
          onClick={toggleTheme}
          edge="end"
          color="inherit"
        >
          {darkState ? <SunIcon /> : <MoonIcon />}
        </IconButton>
      </Toolbar>
    </AppBar>
  );
};

export default NavBar;
