import React, { useState } from "react";
import { Typography } from "@material-ui/core";

import crwn from "../../images/crwn-mockup.png";
import notes from "../../images/mockup-notes.png";
import omnifood from "../../images/omnifood.png";
import ttt from "../../images/ttt.png";
import mytinerary from "../../images/mytinerary.png";
import useStyles from "./styles";

import ProjectDetails from "../ProjectDetails/ProjectDetails";
import { projectIDs } from "../../projects";

export default function ProjectsSection() {
  const {
    title,
    subtitle,
    projectsSection,
    projectsGrid,
    projectImage,
    sectionTitle,
    imageContainer
  } = useStyles();
  const [selectedProject, setSelectedProject] = useState(-1);
  const [isOpen, setIsOpen] = useState(false);

  return (
    <>
      <section className={projectsSection}>
        <div className={sectionTitle}>
          <Typography className={title} variant="h3" component="h3">
            My Projects
          </Typography>
          <Typography
            className={subtitle}
            color="primary"
            variant="h4"
            component="h4"
          >
            Some of my recent projects
          </Typography>
        </div>
        <div className={projectsGrid}>
          <div
            onClick={() => {
              setSelectedProject(projectIDs.CRWN_WORLD);
              setIsOpen(true);
            }}
            className={imageContainer}
            title="Crown world e-commerce"
          >
            <img
              className={projectImage}
              src={crwn}
              alt="Crown world e-commerce"
            />
          </div>
          <div
            onClick={() => {
              setSelectedProject(projectIDs.NOTES_APP);
              setIsOpen(true);
            }}
            className={imageContainer}
            title="Markdown Notes App"
          >
            <img
              className={projectImage}
              src={notes}
              alt="Markdown Notes App"
            />
          </div>
          <div
            onClick={() => {
              setSelectedProject(projectIDs.OMNIFOOD);
              setIsOpen(true);
            }}
            className={imageContainer}
            title="Omnifood"
          >
            <img className={projectImage} src={omnifood} alt="Omnifood" />
          </div>
          <div
            onClick={() => {
              setSelectedProject(projectIDs.TTT);
              setIsOpen(true);
            }}
            className={imageContainer}
            title="Tic Tac Toe"
          >
            <img className={projectImage} src={ttt} alt="Tic Tac Toe" />
          </div>
          <div
            onClick={() => {
              setSelectedProject(projectIDs.MYTINERARY);
              setIsOpen(true);
            }}
            className={imageContainer}
            title="Mytinerary"
          >
            <img className={projectImage} src={mytinerary} alt="Mytinerary" />
          </div>
        </div>
      </section>
      <ProjectDetails
        isOpen={isOpen}
        onRequestClose={() => setIsOpen(false)}
        selected={selectedProject}
      />
    </>
  );
}
