import React from "react";
import { Typography, Grid, IconButton } from "@material-ui/core";
import { GitHub, LinkedIn } from "@material-ui/icons";
import useStyles from "./styles";

export default function Footer() {
  const { link, footer } = useStyles();
  return (
    <footer className={footer}>
      <Grid
        container
        style={{ width: "100%" }}
        alignItems="center"
        spacing={6}
        direction="column"
      >
        <Grid item>
          <Typography
            className={link}
            align="center"
            variant="h4"
            component="a"
            href="mailto:lucasmercado101@gmail.com"
            color="primary"
          >
            Lucasmercado101@gmail.com
          </Typography>
        </Grid>
        <Grid spacing={4} justify="center" direction="row" container item>
          <Grid item>
            <IconButton
              component="a"
              target="_blank"
              href="https://www.linkedin.com/in/lucas-mercado-619179145"
            >
              <LinkedIn color="primary" fontSize="large" />
            </IconButton>
          </Grid>
          <Grid item>
            <IconButton
              component="a"
              href="https://github.com/Lucasmercado101"
              target="_blank"
            >
              <GitHub color="primary" fontSize="large" />
            </IconButton>
          </Grid>
        </Grid>
      </Grid>
    </footer>
  );
}
