import { makeStyles } from "@material-ui/core";

export default makeStyles(({ breakpoints, palette }) => ({
  introSection: {
    display: "block",
    padding: "5em 2em",
    paddingTop: "1em",
    [breakpoints.up("sm")]: {
      display: "grid",
      width: "min-content",
      margin: "0 auto",
      gridColumnGap: "1em",
      gridTemplateAreas: "'img title' 'img subtitle'",
      gridTemplateColumns: "min-content max-content"
    }
  },
  image: {
    gridArea: "img",
    minWidth: "200px",
    position: "relative",
    zIndex: 2,
    maxWidth: "100%",
    display: "block"
  },
  jobTitle: {
    padding: ".25em 1em",
    background: palette.primary.main,
    color: "black",
    fontFamily: "'Playfair Display', serif",
    fontWeight: 200,
    display: "inline-block",
    marginBottom: "1em",
    [breakpoints.up("sm")]: {
      alignSelf: "start",
      gridColumn: "-1 / 1",
      gridRow: 2,
      textAlign: "right",
      position: "relative",
      left: "-1.5em",
      width: "calc(100% + 1.5em)"
    }
  },
  introText: {
    marginBottom: ".25em",
    gridArea: "title",
    letterSpacing: 2,
    fontFamily: "'Playfair Display', serif"
  }
}));
