import { makeStyles } from "@material-ui/core";

export default makeStyles(({ spacing, breakpoints }) => ({
  container: { overflow: "auto", height: "100%", padding: 0 },
  modal: {
    padding: 0,
    display: "grid",
    placeItems: "center",
    "&:focus": {
      outline: "none"
    },
    [breakpoints.up("sm")]: {
      padding: 25
    }
  },
  modalContent: {
    position: "relative",
    width: "100%",
    padding: 15,
    overflow: "auto",
    borderRadius: 0,
    [breakpoints.up("sm")]: {
      padding: 20,
      borderRadius: 4
    },
    [breakpoints.up("lg")]: {
      padding: 50
    }
  },
  imageWrapper: { paddingTop: "60%", position: "relative" },
  imageClass: {
    position: "absolute",
    width: "100%",
    height: "100%",
    objectFit: "cover",
    objectPosition: "center",
    inset: 0
  }
}));
