import { makeStyles } from "@material-ui/core";

export default makeStyles(({ palette }) => ({
  sectionTitle: {
    width: "100%",
    padding: 25,
    textAlign: "center"
  },
  title: {
    color: palette.common.white,
    fontFamily: "'Playfair Display', serif",
    fontWeight: "bolder",
    marginBottom: ".25em"
  },
  subtitle: {
    color: palette.primary.main,
    fontFamily: "'Playfair Display', serif",
    fontWeight: "bold"
  },
  projectsSection: {
    padding: "3em 0",
    background: palette.type === "light" ? "#303030" : "#555"
  },
  projectsGrid: {
    display: "grid",
    gap: "15px",
    gridTemplateColumns: "repeat(auto-fit, minmax(400px, 1fr))",
    padding: 25
  },
  imageContainer: {
    cursor: "pointer",
    overflow: "hidden",
    background: palette.primary.main,
    "&:focus": {
      outline: "3px solid #16e0bd",
      outlineOffset: 3
    }
  },

  projectImage: {
    width: "100%",
    height: "100%",
    objectFit: "cover",
    objectPosition: "center",
    transition:
      "transform 750ms cubic-bezier(0.5, 0, 0.5, 1), opacity 250ms linear, -webkit-transform 750ms cubic-bezier(0.5, 0, 0.5, 1)",
    "&:hover": {
      opacity: 0.8,
      transform: "scale(1.25)"
    }
  }
}));
