import { createContainer } from "unstated-next";
import { useState } from "react";

const initialState = window.localStorage.getItem("darkTheme") === "true";

function useCounter() {
  const [darkState, setDarkState] = useState(initialState);
  const toggleTheme = () => {
    window.localStorage.setItem("darkTheme", JSON.stringify(!darkState));
    setDarkState(!darkState);
  };
  return { darkState, toggleTheme };
}

export default createContainer(useCounter);
